apiURL = 'http://localhost:8000/api';
URL = 'http://localhost:8000';


// To dos APIs

export const fetchToDos = async () => {
    const res = await fetch(`${apiURL}/all`);
    const dataFetched = res.json();
    return dataFetched;
};

export const postToDo = async (newTodo) => {
    const data = { 
        id: newTodo.id,
        description: newTodo.description,
        status: newTodo.status
    };
    const dataJson = JSON.stringify(data);
    const res = await fetch(`${apiURL}/create`, {
        method: 'POST',
        headers: {
            'Content-type': 'application/json',
        },
        body: dataJson,
    });
    console.log(res.status);
    return null;
};

export const deleteToDo = async (id) => {
    const res = await fetch(`${apiURL}/remove/${id}`, {
        method: 'DELETE',
        headers: {
            'Content-type': 'application/json',
        },
    });
    console.log(res.status);
    return null;
};

export const checkToDoStatus = async (id) => {
    const res = await fetch(`${apiURL}/updateStatus/${id}`, {
        method: 'PUT',
        headers: {
            'Content-type': 'application/json',
        },
    });
    console.log(res.status);
    return null;
};

export const fetchToDo = async (id) => {
    const res = await fetch(`${apiURL}/${id}`);
    const dataFetched = res.json();
    return dataFetched;
};

export const setListOrderAsync = async (id, newListOrder) => {
    const newListOrderJson = JSON.stringify(newListOrder);
    const res = await fetch(`${apiURL}/setListOrder/${id}`, {
        method: 'PUT',
        headers: {
            'Content-type': 'application/json',
        },
        body: newListOrderJson,
    });
    console.log(res.status);
    return null;
};

export const fetchListOrder = async (id) => {
    const res = await fetch(`${apiURL}/getListOrder/${id}`);
    const listOrderFetched = res.json();
    return listOrderFetched;
};


// Login API

export const loginApi = async (email, password) => {
    const data = { 
        email: email,
        password: password
    };
    const dataJson = JSON.stringify(data);
    const res = await fetch(`${URL}/login`, {
        method: 'POST',
        headers: {
            'Content-type': 'application/json',
            withCredentials: true
        },
        body: dataJson,
    });
    console.log(res);
    console.log(res.status);
    return null;
};


var corsOptions = {
    origin: 'http://localhost:8000',
    optionsSuccessStatus: 200
}

export const getUser = async () => {
    const res = await fetch(`${URL}/profile/getuser`, cors(corsOptions), { 
        method: 'GET',
        withCredentials: true,
    });
    const dataFetched = res.json();
    console.log(res.headers);
    return dataFetched;
};